import React from 'react';

import { AddIcon, N_Icon } from '../../../assets/icons';
import ImageCustom from '../../ImageCustom';
import style from './styles.module.scss';

type PropsInformationItem = {
  content: string;
};

type HeaderProps = {
  title: string;
};

const Header = ({ title }: HeaderProps) => {
  return (
    <div className={style.headerWrapper}>
      <div className={style.headerItem}>
        <span>{title}</span>
        <div className={style.end}>
          <ImageCustom src={AddIcon} />
          <p>더보기</p>
        </div>
      </div>
      <div className={style.separator} />
    </div>
  );
};

type RowProps = {
  content: string;
  time: string;
  hadN: boolean;
  top?: boolean;
};

const Row = ({ content, time, hadN, top }: RowProps) => {
  return (
    <div className={style.rowWrapper} style={{ marginTop: top ? 25 : 17 }}>
      <div className={style.contentWrap}>
        <div className={style.content}>{content}</div>
        {hadN && <ImageCustom style={{ marginLeft: 6 }} src={N_Icon} />}
      </div>
      <div className={style.time}>{time}</div>
    </div>
  );
};

function InformationItem({ content }: PropsInformationItem) {
  return (
    <div className={style.information_item_wrap}>
      <Header title={content} />
      <div className={style.body_wrap}>
        <Row content='추석연휴 관련 공지 (9/9~9/12 휴진)' time='08.29' hadN top />
        <Row content='공휴일 휴진 안내 (6/1,6/6 휴진)' time='05.26' hadN={false} />
        <Row content='어린이날 휴진 안내 (5/5 휴진)' time='05.04' hadN={false} />
        <Row content='개원기념일 오전 진료 안내 (5/3 오후...' time='04.26' hadN={false} />
        <Row content='대통령선거 임시공휴일 휴진 안내 (3/...' time='03.08' hadN={false} />
      </div>
    </div>
  );
}

export default React.memo(InformationItem);
