import React from 'react';

import { Footer1, Footer2, Footer3, Footer4, Footer5, Footer6 } from '../../../assets/icons';
import ImageCustom from '../../ImageCustom';
import style from './styles.module.scss';

type Props = {
  // content: string;
};

function FooterHeader({}: Props) {
  return (
    <div className={style.headerContainer}>
      <div className={style.headerWrap}>
        <span>협력의료기관 |</span>
        <ImageCustom src={Footer1} style={{ marginLeft: 46 }} />
        <ImageCustom src={Footer2} style={{ marginLeft: 90 }} />
        <ImageCustom src={Footer3} style={{ marginLeft: 90 }} />
        <ImageCustom src={Footer4} style={{ marginLeft: 90 }} />
        <ImageCustom src={Footer5} style={{ marginLeft: 90 }} />
        <ImageCustom src={Footer6} style={{ marginLeft: 90 }} />
      </div>
    </div>
  );
}

export default React.memo(FooterHeader);
