import './tabStyle.scss';

import React from 'react';
import { Carousel } from 'react-responsive-carousel';
import { Tab, TabList, TabPanel, Tabs } from 'react-tabs';

import { DayPassImg, DayPassMobileImg } from '../../../assets/images';
import useMobile from '../../../Hooks/useMobile';
import style from './styles.module.scss';

function ItemDayPass() {
  const { isMobile } = useMobile();
  return (
    <>
      {isMobile && <div className={style.title_mobile}>재활의학과 전문의</div>}
      <div
        className={style.item_pass_wrap}
        style={{
          backgroundImage: `url(${isMobile ? DayPassMobileImg : DayPassImg})`,
          backgroundPosition: 'center',
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
        }}
      >
        {!isMobile && <div className={style.title}>재활의학과 전문의</div>}
        <div className={style.sort_content}>
          김기태 <span>원장</span>
        </div>
        <div className={style.content}>
          {'• 서울예스병원 재활의학과 원장\n' +
            '• 여수애양병원 명예원장\n' +
            '• 서울대학교 의과대학 졸업\n' +
            '• 서울대학교 의과대학 석/박사'}
        </div>
        <button className={style.action}>의료진 자세히 보기</button>
        {!isMobile && <div className={style.noteText}>* 내원 전 의료진별 진료시간표를 꼭 확인해주세요.</div>}
      </div>
    </>
  );
}

function CarouselItem() {
  return (
    <Carousel thumbWidth={0} showStatus={false} showIndicators={false} className={'day_pass'} infiniteLoop={true}>
      <div>
        <ItemDayPass />
      </div>
      <div>
        <ItemDayPass />
      </div>
      <div>
        <ItemDayPass />
      </div>
    </Carousel>
  );
}

function DayPass(): JSX.Element {
  return (
    <div className={style.day_pass_wrap}>
      <div className={style.title}>
        <span>YES!</span>
        탁월한 의료진
      </div>
      <div className={style.sub_title}>남다른 의료진의 고난도 치료를 받아보세요.</div>
      <Tabs>
        <TabList>
          <Tab>재활의학과</Tab>
          <Tab>정형외과</Tab>
          <Tab>신경외과</Tab>
        </TabList>
        <TabPanel>
          <CarouselItem />
        </TabPanel>
        <TabPanel>
          <CarouselItem />
        </TabPanel>
        <TabPanel>
          <CarouselItem />
        </TabPanel>
      </Tabs>
    </div>
  );
}

export default DayPass;
