import React from 'react';
import Carousel from 'react-material-ui-carousel';

import {
  Course1Img,
  Course2Img,
  Course3Img,
  Course4Img,
  Course5Img,
  Course6Img,
  Course7Img,
  Course8Img,
} from '../../../assets/images';
import useMobile from '../../../Hooks/useMobile';
import style from './styles.module.scss';
type PropsMedicalItem = {
  bgImg: string;
  title: string;
  content: string;
};

function MedicalItem({ bgImg, title, content }: PropsMedicalItem) {
  return (
    <div className={style.item_wrap} style={{ backgroundImage: `url(${bgImg})` }}>
      <span className={style.title}>{title}</span>
      <span className={style.content}>{content}</span>
    </div>
  );
}

function MedicalCourse(): JSX.Element {
  const { isMobile } = useMobile();
  return (
    <div className={style.medical_course_wrap}>
      <div className={style.title}>
        <span>YES!</span> 확실한 진료
      </div>
      <div className={style.sub_title}>서울예스병원만의 높은만족도 치료를 경험해보세요.</div>
      {isMobile ? (
        <div className={style.list_course_wrap_mobile}>
          <Carousel
            fullHeightHover={true}
            autoPlay={false}
            navButtonsAlwaysInvisible={true}
            swipe={true}
            indicatorIconButtonProps={{
              style: {
                width: 8,
                height: 8,
                background: '#E9EBF1',
                borderRadius: 10,
                color: '#E9EBF1',
                marginLeft: 8,
              },
            }}
            activeIndicatorIconButtonProps={{
              style: {
                width: 15,
                height: 8,
                background: '#0638BE',
                borderRadius: 10,
                color: '#0638BE',
                marginLeft: 8,
              },
            }}
            IndicatorIcon={
              <div
                style={{
                  width: 8,
                  height: 8,
                  marginLeft: 8,
                  borderRadius: '50%',
                  fontSize: 8,
                }}
              />
            }
          >
            <MedicalItem bgImg={Course1Img} title={'관절센터'} content={'고난도 치료와 높은 만족도'} />
            <MedicalItem bgImg={Course2Img} title={'관절경수술'} content={'어깨도 무릎도 팔도\n' + '이제 간단히'} />
            <MedicalItem
              bgImg={Course3Img}
              title={'꼬리뼈내시경\n' + '레이저시술'}
              content={'허리통증의\n' + '근본적 원인 해결'}
            />
            <MedicalItem
              bgImg={Course4Img}
              title={'YES 척추내시경'}
              content={'의료기술 강국의\n' + '척추내시경 1등 병원'}
            />
            <MedicalItem
              bgImg={Course5Img}
              title={'척추센터'}
              content={'해외 의료진이 배우기위해\n' + '방문하는 서울예스병원'}
            />
            <MedicalItem bgImg={Course6Img} title={'신경성형술'} content={'특수 카테터로 척추질환 통증해방'} />
            <MedicalItem bgImg={Course7Img} title={'비수술통증센터'} content={'치료율은 높히고, 재발율은 낮추고'} />
            <MedicalItem bgImg={Course8Img} title={'내과'} content={'우리 경기, 용인지역 평생주치의'} />
          </Carousel>
        </div>
      ) : (
        <div className={style.list_course_wrap}>
          <MedicalItem bgImg={Course1Img} title={'관절센터'} content={'고난도 치료와 높은 만족도'} />
          <MedicalItem bgImg={Course2Img} title={'관절경수술'} content={'어깨도 무릎도 팔도\n' + '이제 간단히'} />
          <MedicalItem
            bgImg={Course3Img}
            title={'꼬리뼈내시경\n' + '레이저시술'}
            content={'허리통증의\n' + '근본적 원인 해결'}
          />
          <MedicalItem
            bgImg={Course4Img}
            title={'YES 척추내시경'}
            content={'의료기술 강국의\n' + '척추내시경 1등 병원'}
          />
          <MedicalItem
            bgImg={Course5Img}
            title={'척추센터'}
            content={'해외 의료진이 배우기위해\n' + '방문하는 서울예스병원'}
          />
          <MedicalItem bgImg={Course6Img} title={'신경성형술'} content={'특수 카테터로 척추질환 통증해방'} />
          <MedicalItem bgImg={Course7Img} title={'비수술통증센터'} content={'치료율은 높히고, 재발율은 낮추고'} />
          <MedicalItem bgImg={Course8Img} title={'내과'} content={'우리 경기, 용인지역 평생주치의'} />
        </div>
      )}
    </div>
  );
}

export default MedicalCourse;
