import React from 'react';

import { IntroductionImg, ParkingImg, ReservationImg, TimetableImg, WayImg } from '../../../assets/images/icons';
import useMobile from '../../../Hooks/useMobile';
import ImageCustom from '../../ImageCustom';
import style from './styles.module.scss';

type PropsItem = {
  content: string;
  src: string;
  className?: string;
  action?: () => void;
};
function Item({ content, src, action, className }: PropsItem) {
  return (
    <div className={`${style.item_wrap} ${className}`}>
      <div className={style.image_wrap}>
        <ImageCustom src={src} />
      </div>

      <span>{content}</span>
    </div>
  );
}

function TreatmentHours() {
  return (
    <div className={style.treatment_hour_wrap}>
      <div className={style.slice} />
      <div className={style.body}>
        <span className={style.title}>진료시간</span>
        <span className={style.time}>평일 09:30 ~ 18:00</span>
        <span className={style.time}>평일 09:30 ~ 18:00</span>
        <span className={style.time}>평일 09:30 ~ 18:00</span>
        <span className={style.note}>*일요일/공휴일은 휴진, 토요일은 점심시간 없이 진료합니다.</span>
      </div>
    </div>
  );
}

function HomeMainFunction(): JSX.Element {
  const { isMobile } = useMobile();

  if (isMobile) {
    return (
      <div className={style.home_main_function_wrap}>
        <div className={style.wrap_1}>
          <Item content={'진료시간표'} src={TimetableImg} />
          <Item content={'온라인 예약'} src={ReservationImg} className={style.left60} />
        </div>
        <div className={style.wrap_2}>
          <Item content={'의료진 소개'} src={IntroductionImg} className={style.left60} />
          <Item content={'오시는 길'} src={WayImg} className={style.left60} />
          <Item content={'주차안내'} src={ParkingImg} className={style.left60} />
        </div>
      </div>
    );
  }

  return (
    <div className={style.home_main_function_wrap}>
      <Item content={'진료시간표'} src={TimetableImg} />
      <Item content={'온라인 예약'} src={ReservationImg} className={style.left60} />
      <Item content={'의료진 소개'} src={IntroductionImg} className={style.left60} />
      <Item content={'오시는 길'} src={WayImg} className={style.left60} />
      <Item content={'주차안내'} src={ParkingImg} className={style.left60} />
      <TreatmentHours />
    </div>
  );
}

export default HomeMainFunction;
