import React from 'react';

import { MoreIcon, SearchWhite } from '../../assets/icons';
import { Logo } from '../../assets/images/icons';
import useMobile from '../../Hooks/useMobile';
import ImageCustom from '../ImageCustom';
import style from './styles.module.scss';

function Header(): JSX.Element {
  const { isMobile } = useMobile();
  return (
    <div className={style.header_wrap}>
      <ImageCustom src={Logo} className={style.logo} />
      <div className={style.tab_wrap}>
        <div className={style.tab}>진료안내</div>
        <div className={style.tab}>예약/상담</div>
        <div className={style.tab}>의료진/진료과</div>
        <div className={style.tab}>고객센터</div>
        <div className={style.tab}>병원소개</div>
      </div>
      <div className={style.right_header}>
        <div className={style.find}>
          <ImageCustom src={SearchWhite} />
        </div>
        {isMobile ? <ImageCustom src={MoreIcon} /> : <div className={style.textContainer}>로그인</div>}
      </div>
    </div>
  );
}

export default Header;
