import 'react-responsive-carousel/lib/styles/carousel.min.css';
import '../../../assets/styles/custom_carousel.scss';

import React from 'react';
import { Carousel } from 'react-responsive-carousel';

import { BannerImg, BannerMobileImg } from '../../../assets/images';
import useMobile from '../../../Hooks/useMobile';
import style from './styles.module.scss';

function BannerItem() {
  const { isMobile } = useMobile();
  return (
    <div className={style.banner_wrap} style={{ backgroundImage: `url(${isMobile ? BannerMobileImg : BannerImg})` }}>
      <div className={style.title}>보호자의 믿음을 바탕으로</div>
      <div className={style.content}>{'효로 모시는 \n' + '간병서비스'}</div>
      <div className={style.content_sub}>
        <p>24시간 전담간호, 건강보험 적용까지</p>
        <p>간호간병 통합서비스를 서울예스병원에 맡겨보세요!</p>
      </div>
    </div>
  );
}

function Banner(): JSX.Element {
  return (
    <Carousel thumbWidth={0} showStatus={false} className={'home_banner'} infiniteLoop={true}>
      <div>
        <BannerItem />
      </div>
      <div>
        <BannerItem />
      </div>
      <div>
        <BannerItem />
      </div>
      <div>
        <BannerItem />
      </div>
    </Carousel>
  );
}

export default Banner;
