import React from 'react';

import InformationHeader from './Header';
import InformationItem from './InformationItem';
import style from './styles.module.scss';

function Information(): JSX.Element {
  return (
    <div>
      <InformationHeader />
      <div className={style.information_wrap}>
        <InformationItem content={'공지사항'} />
        <InformationItem content={'언론보도'} />
        <InformationItem content={'채용공고'} />
      </div>
    </div>
  );
}

export default Information;
