import React from 'react';

import { BlogIcon, FacebookIcon, InstagramIcon, TalkIcon, YoutubeIcon } from '../../../assets/icons';
import ImageCustom from '../../ImageCustom';
import style from './styles.module.scss';

type Props = {
  // content: string;
};

function FooterBottom({}: Props) {
  return (
    <div className={style.bottomContainer}>
      <div className={style.buttonWrap}>
        <span>서비스이용약관 | 개인정보취급방침</span>
        <div className={style.imageContainer}>
          <ImageCustom src={TalkIcon} className={style.icon} />
          <ImageCustom src={BlogIcon} style={{ marginLeft: 16 }} className={style.icon} />
          <ImageCustom src={YoutubeIcon} style={{ marginLeft: 16 }} className={style.icon} />
          <ImageCustom src={FacebookIcon} style={{ marginLeft: 16 }} className={style.icon} />
          <ImageCustom src={InstagramIcon} style={{ marginLeft: 16 }} className={style.icon} />
        </div>
      </div>
    </div>
  );
}

export default React.memo(FooterBottom);
