import React from 'react';

import { MiddleFooterIcon, MiddleFooterNumberphone } from '../../../assets/icons';
import ImageCustom from '../../ImageCustom';
import style from './styles.module.scss';

type Props = {
  // content: string;
};

function FooterMiddle({}: Props) {
  return (
    <div className={style.midContainer}>
      <div className={style.midWrap}>
        <ImageCustom src={MiddleFooterIcon} style={{ width: 149, height: 20 }} className={style.icon} />
        <div className={style.contentContainer}>
          <ImageCustom src={MiddleFooterNumberphone} className={style.phoneNumber} />
          <div className={style.textContainer}>
            <span>사업자 등록번호 142-91-25225</span>
            <span style={{ marginLeft: 13 }}>대표 도현우</span>
            <span style={{ marginLeft: 13 }}>경기도 용인시 수지구 용구대로 2736 (죽전동 902-2)</span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default React.memo(FooterMiddle);
