import React from 'react';

import HomeBody from '../../containers/HomeBody';

function HomePage(): JSX.Element {
  return (
    <div
      style={{
        alignSelf: 'center',
        flex: 1,
        display: 'flex',
        alignItems: 'center',
      }}
    >
      <HomeBody />
    </div>
  );
}

export default HomePage;
